const noteGetters: GetterTree<PageState, RootState> = {
  getPages(state): Pages {
    return state.pages;
  },
  getDemoPage(state): Page | undefined {
    return state.pages.find(
      i => i._id === "fb658d57-4653-4876-89b3-80ff9f60e3d6"
    );
  },
  getPage: state => (id: string) =>
    state.pages.find(i => {
      return i._id === id;
    }),
  totalTasks(state, id: string): number {
    const page = state.pages.find(i => i._id === id);
    if (page !== undefined) {
      return page.notesIn.length;
    }
    return -1;
  },
  getNotes(state, id): Notes | undefined {
    const todoCard = state.pages.find(i => i._id === id);
    if (todoCard !== undefined) {
      return todoCard.notesIn;
    }
    return undefined;
  },
  totalDoneTasks: (state, getters) => {
    return getters.getTodos.filter((i: Note) => i.checked === true).length;
  },
  latestNote(state): Page {
    return state.pages.slice(-1)[0];
  },
};

const mutations: MutationTree<PageState> = {
  addPage(state, page: Page) {
    if (state.pages.find(i => i._id === page._id)) {
      state.pages.splice(
        state.pages.findIndex(i => i._id === page._id),
        1,
        page
      );
    } else {
      state.pages.push(page);
    }
  },
  deletePage(state, id: string) {
    const search = state.pages.filter(i => i._id !== id);
    state.pages = search;
  },
  updatePage(state, page: Page) {
    // const idx = ;
    // state.pages[idx].title = page.title;
    // state.pages[idx].notesIn = page.notesIn;
    state.pages.splice(
      state.pages.findIndex(i => i._id === page._id),
      1,
      page
    );
  },
  resetPages(state){
    state.pages = state.pages.filter(i => i._id ==="fb658d57-4653-4876-89b3-80ff9f60e3d6");
  },
  cleanPages(state,ids){
    ids.push("fb658d57-4653-4876-89b3-80ff9f60e3d6");
    const unnecessaryPageIdx:number[]=[]
    state.pages.forEach((element:Page,index:number)=>{
      if(!ids.include(element._id)){
        unnecessaryPageIdx.push(index);
      }
    })
    unnecessaryPageIdx.forEach((element:number)=>{
        state.pages.splice(element,1);
    })
  }
};

const actions: ActionTree<PageState, RootState> = {
  addPage(context) {
    // axios.defaults.withCredentials = true;
    axios.defaults.headers.common["authorization"] = store.state.auth.token;

    axios
      .post(
        `http://${process.env.VUE_APP_URL}:${process.env.VUE_APP_PORT}/api/page/add`
        // { withCredentials: true }
      )
      .then(resp => {
        const page = resp.data;
        context.commit("addPage", page);
      })
      .catch(err => {
        if (err.response != undefined) {
          if (err.response.status === 500) {
            router.push("/500");
          } else if (err.response.status === 404) {
            router.push("/404");
          } else {
            console.log(err);
          }
        }
      });
  },
  deletePage(context, id: string) {
    axios.defaults.headers.common["authorization"] = store.state.auth.token;

    axios
      .delete(
        `http://${process.env.VUE_APP_URL}:${process.env.VUE_APP_PORT}/api/page/` + id
        //  { withCredentials: true }
      )
      .then(resp => {
        context.commit("deletePage", id);
      })
      .catch(err => {
        if (err.response != undefined) {
          if (err.response.status === 500) {
            router.push("/500");
          } else if (err.response.status === 404) {
            router.push("/404");
          } else {
            console.log(err);
          }
        }
      });
  },
  updatePage(context, page) {
    axios.defaults.headers.common["authorization"] = store.state.auth.token;

    // axios.defaults.withCredentials = true;
    axios
      .put(
        `http://${process.env.VUE_APP_URL}:${process.env.VUE_APP_PORT}/api/page/` + page._id,
        page
        // { withCredentials: true }
      )
      .then(resp => {
        context.commit("updatePage", page);
      })
      .catch(err => {
        if (err.response != undefined) {
          if (err.response.status === 500) {
            router.push("/500");
          } else if (err.response.status === 404) {
            router.push("/404");
          } else {
            console.log(err);
          }
        }
      });
  },
  getPages(context) {
    axios.defaults.headers.common["authorization"] = store.state.auth.token;

    axios
      .get(
        `http://${process.env.VUE_APP_URL}:${process.env.VUE_APP_PORT}/api/page`
        // { withCredentials: true }
      )
      .then(resp => {
        const pages = resp.data.pages;
        const ids:string[]=[];
        pages.forEach((element: Page) => {
          context.commit("addPage", element);
          ids.push(element._id);
        });
        context.commit("cleanPages",ids);
      })
      .catch(err => {
        if (err.response != undefined) {
          if (err.response.status === 500) {
            router.push("/500");
          } else if (err.response.status === 404) {
            router.push("/404");
          } else {
            console.log(err);
          }
        }
      });
  },
  getPage(context, id) {
    axios.defaults.headers.common["authorization"] = store.state.auth.token;

    axios
      .get(
        `http://${process.env.VUE_APP_URL}:${process.env.VUE_APP_PORT}/api/page/` + id
        //  { withCredentials: true }
      )
      .then(resp => {
        const page = resp.data.page;
        context.commit("addPage", page);
      })
      .catch(err => {
        if (err.response != undefined) {
          if (err.response.status === 500) {
            router.push("/500");
          } else if (err.response.status === 404) {
            router.push("/404");
          } else {
            console.log(err);
          }
        }
      });
  },
  copyPage(context, id) {
    // axios.defaults.withCredentials = true;
    axios.defaults.headers.common["authorization"] = store.state.auth.token;

    axios
      .post(
        `http://${process.env.VUE_APP_URL}:${process.env.VUE_APP_PORT}/api/page/copy/` + id
        // { withCredentials: true }
      )
      .then(resp => {
        const page = resp.data;
        context.commit("addPage", page);
      })
      .catch(err => {
        if (err.response != undefined) {
          if (err.response.status === 500) {
            router.push("/500");
          } else if (err.response.status === 404) {
            router.push("/404");
          } else {
            console.log(err);
          }
        }
      });
  },
  resetPages(context){
    context.commit("resetPages");
  }
};

const note = {
  namespaced: true,
  state: noteState,
  getters: noteGetters,
  mutations,
  actions
};

export default note;
